.first-screen{
    background-color: var(--text-color-dark);
    padding-bottom: 200px;
    .col-6{
        padding-top: 131px;
    }
    .first-screen_action{
        .base{
            display: flex;
            max-width: 365px;
            width: 100%;
            height: 40px;
            padding: 4px 5px;
            border-radius: 32px;
            background: #ffffff1a;
            .saving{
                padding: 4px 16px;
                border-radius: 32px;
                background-color: #fff;
                font-weight: 500;
                letter-spacing: 0.64px;
                text-transform: uppercase;
                display: inline-flex;
                text-wrap: nowrap;
            }
            .time-saving{
                padding-top: 4px;
                padding-left: 16px;
                line-height: 28px;
                letter-spacing: 0.16px;
                color: var(--text-color);
                text-wrap: nowrap;
            }
        }
    }
    .first-screen_title{
        margin-top: 24px;
        font-size: 64px;
        font-weight: 700;
        line-height: normal;
        color: var(--text-color);
    }
    .first-screen_subtitle{
        max-width: 435px;
        margin-top: 24px;
        line-height: 28px;
        letter-spacing: 0.16px;
        color: var(--text-color);
    }
    .first-screen_button{
        margin-top: 32px;
        display: inline-flex;
        padding: 16px 16px 15px 24px;
        align-items: center;
        gap: 24px;
        border-radius: 32px;
        background: #3671E9;
        color: var(--text-color);
        font-size: 18px;
        font-weight: 500;
        svg{
            width: 32px;
            height: 32px;
            padding: 4px;
            background-color: #fff;
            border-radius: 16px;
        }
    }
    .first-screen_button:hover{
        background-color: #fff;
        color: black;
        svg{
            background-color: #3671E9;
            path{
                fill: #fff;
            }
        }
    }

}
